*{
  box-sizing: border-box;
}

@font-face {
  font-family: 'Minisystem';
  src: url('./fonts/minisystem.ttf') format('truetype');
}

body{
  background: #2a3347;
  color: #e0d7d7;
}

.App {
  background: #3f4c6b;
  text-align: center;
  width: 850px;
  margin: 20px auto;
  border-top: 20px solid #22293a;
  /* border: 2px solid #e0d7d7; */
  /* border-radius:20px; */
  padding: 25px;
  display: block;
  box-shadow: 0px 10px 50px rgba(0,0,0,.3);
}

.infoScreen{
  background-color: #101010;
  border-radius: 4px;
  width: 50%;
  display: inline-block;
  text-align:left;
  padding:5px;
}
.infoScreen p{
  line-height: 15px;
}
.infoScreen label{
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  margin: 0 10px 0 0;
}
.infoScreen span{
  font-family: 'Minisystem';
  display: inline-block;
  font-size: 24px;
}

.infoScreen .tempo{
  font-size: 30px;
}

.transport-controls-wrapper{
  width:50%;
  display: inline-block;
}
.transport-controls-wrapper span{
  display: inline-block;
  font-family: 'Minisystem';
  font-size: 24px;
  background-color: rgba(0,0,0,.8);
  padding: 10px;
  width: 150px;
  text-align: right;
}
.stop,
.play{
  appearance: none;
  border: 2px solid #e0d7d7;
  appearance: none;
  height: 50px;
  width: 75px;
  border-radius: 3px;
  background-color: #606c88;
  color: #e0d7d7;
  text-transform: uppercase;
  margin: 0 5px;
  font-weight:bolder;
  font-size:14px;
}

.play[data-active=true]{
  background: #e0d7d7;
  color: #3f4c6b;
}

.stop:active,
.play:active{
  opacity: .7;
}

.trackselection-wrapper{
  height: 60px;
  width: 700px;
  display:inline-block;
  margin: 30px 0;
}
.clearcontrols{
  display: inline-block;
  width: 100%;
  height: 50px;
}
.trackselector-wrapper{
  width:80px;
  height:70px;
  display:inline-block;
  padding:5px;
}

.trackselector-wrapper button,
.clearcontrols button{
  width: 70px;
  height: 30px;
  line-height: 25px;
  font-size: 10px;
  font-weight: 700;
  appearance: none;
  padding:none;
  text-transform: uppercase;
  border: 2px solid #e0d7d7;
  color: #e0d7d7;
  background-color:#606c88;
  -webkit-appearance: none;
  border-radius:2.5px;
  cursor: pointer;
  margin: 0 0 5px;
}

.clearcontrols button{
  margin: 5px;
  width: 150px;
  background-color: #101010;
}

.trackselector-wrapper button[data-selected=true]{
  background-color: #e0d7d7;
  color: #606c88;
  transition: background-color .1s, color .1s; 
}
.trackselector-wrapper button[data-muted=true]{
  background-color: #101010;
  color: #8f8b8b;
  border-color: #8f8b8b;
}
.volumeslider{
  display:inline-block;
  width:20px;
  height: 80px;
  padding: 0;
  margin: 0 2.5px;
}
.volumeslider input{
  width: 80px;
  height: 20px;
  margin: 0;
  transform-origin: 40px 40px;
  transform: rotate(-90deg);
}

.notebars{
  width: 800px;
  height: 10px;
  margin: 0 0 15px;
}
.notebar{
  background-color: #606c88;
  width: 189px;
  height: 10px;
  margin: 0 5px 0 5px;
  display:inline-block;
  border: 1px solid #606c88;
}
/* .notebar:first-of-type{
  margin: 0 5px 0 0;
} */
.notebar:last-of-type{
  margin: 0 0 0 5px;
}
.sequencer-wrapper{
  margin: 0 0 0;
  /* border:5px solid #aaa4a4; */
  border-radius: 8px;
  display:inline-block;
  padding: 0 0px;
}

.sequencer-button-wrapper{
  display:inline-block;
  margin: 0 0 10px;
  width: 50px;
}
.sequencer--button{
  position:relative;
  height:70px;
  width:40px;
  border:1px solid #000;
  display:inline-block;
  margin: 0 5px;
  font-size: 24px;
  line-height:70px;
  border-radius:2px;
  background: linear-gradient(to bottom, #606c88 0%,#3f4c6b 100%); 
  cursor: pointer;
}

.sequencer--button.active{
  background: linear-gradient(to bottom, #b6bac4 0%,#6e7177 100%);
  box-shadow: 0 0 10px rgba(255,255,255,.3);
}

.sequencer--button:active{
  opacity:.7;
}

.sequencer--button.chase,
.sequencer--button.active.chase{
  background: linear-gradient(to bottom, #ec3a3a 0%,#7d1a1a 100%);
}

.sequencer-button-wrapper:nth-of-type(4n+1) .sequencer--button::after{
  content:'';
  width: 20px;
  height: 5px;
  border-radius: 1.5px;
  display:block;
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: #000;
}

.sequencer-button-wrapper label{
  position:relative;
  display:block;
  text-align:center;
  margin: 5px 5px 0;
  font-size: 11px;
  color: #aaa4a4;
  font-weight:800;
  font-family: Helvetica, sans-serif;
}

